<template>
  <div>
    <StatFilter class="map-filter" v-on:get-data="getData" />
    <DataTableHeaderClicker
      :tableHeaders="tableHeaders"
      :tableItems="tableItems"
      v-on:get-chart="getChart"
      v-on:download="download"
    />
    <v-card
      class="chart-card"
      style="border-radius: 15px; max-height: 500px; margin-bottom: 60px"
      v-if="loaded"
      elevation="0"
    >
      <Chart :chart-data="chartData" />
    </v-card>
  </div>
</template>

<style lang="scss" scoped>
@import "@/styles/common.scss";
@import "@/styles/views/stat.scss";
</style>

<script>
import StatFilter from "@/components/stat/StatFilter.vue";
import DataTableHeaderClicker from "@/components/stat/DataTableHeaderClicker.vue";
import Chart from "@/components/Chart.vue";

export default {
  name: "Stat",
  components: {
    StatFilter,
    DataTableHeaderClicker,
    Chart,
  },
  data() {
    return {
      loaded: false,
      chartData: {},

      tableItems: [],

      filterParams: {},

      timeGroups: {
        " ": null,
        "2.00 - 11.00": 0,
        "11.00 - 16.00": 1,
        "16.00 - 21.00": 2,
        "21.00 - 2.00": 3,
      },

      tableHeaders: [
        { text: "", value: "datetime" },
        { text: "", value: "parent_region" },
        { text: "", value: "region" },
        { text: "", value: "address" },
        { text: "  ", value: "category" },
        { text: " ", value: "organization" },
        { text: "  ", value: "criminal_type" },
        { text: " ", value: "employee" },
        { text: " ", value: "crime_qualification" },
        { text: "  ", value: "yk_qualification" },
        { text: "", value: "severity" },
        { text: "", value: "crime_direction" },
        { text: "", value: "damage" },
        { text: "", value: "money" },
        { text: " ", value: "drug_name" },
        { text: " ", value: "drug_weight" },
        { text: " ", value: "victim_number" },
        { text: " ", value: "dead_num" },
        { text: " ", value: "is_operative" },
        { text: "/", value: "is_attempt" },
        { text: "", value: "light" },
      ],
    };
  },

  methods: {
    download(format) {
      let url = new URL(`${this.$store.state.backendUrl}/file/get/${format}/`);
      if (this.filterParams) {
        Object.keys(this.filterParams).forEach((key) => {
          if (this.filterParams[key]) {
            try {
              url.searchParams.set(key, this.filterParams[key].toISOString());
            } catch {
              url.searchParams.set(key, this.filterParams[key]);
            }
          }
        });
      }
      window.open(url.href);
    },

    getData(filterParams) {
      this.filterParams = filterParams;
      console.log(filterParams)
      axios
        .get(`${this.$store.state.backendUrl}/map/detail/`, {
          params: filterParams
        })
        .then((r) => {
          this.tableItems = r.data;
        });
    },

    async getChart(column) {
      this.filterParams.time_group = this.timeGroups[this.filterParams.time_group];

      if (column != undefined) {
        this.loaded = false;
        await axios
          .get(`${this.$store.state.backendUrl}/map/plot-diagram/${column}/`, {
            params: this.filterParams,
          })
          .then((response) => {
            let name = "";
            for (let i = 0; i < this.tableHeaders.length; i++) {
              if (this.tableHeaders[i].value == column) {
                name = this.tableHeaders[i].text;
              }
            }

            this.chartData = {
              labels: response.data.map((x) => x.field),
              datasets: [
                {
                  label: `   ${name}`,
                  borderColor: "#EDAC48",
                  pointBackgroundColor: "#EDAC48",
                  borderWidth: 1,
                  pointBorderColor: "white",
                  backgroundColor: "#EDAC48",
                  data: response.data.map((x) => x.c),
                },
              ],
            };

            this.loaded = true;
          });
      }
    },
  },
};
</script>